div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
h1{
    text-align: center;
    font-size: 4rem;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    text-shadow: 5px 5px 15px white;
    position: relative;
    animation-name: hTag;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    margin-top: 30vh;
}
@keyframes hTag {
    0%{
        left: 0vh;
    }
    25%{
        left:10vh;
    }
    50%{
        left:-10vh;
    }
    100%{
        left: 0vh;
    }
}
button{
    width: 15vw;
    height: 5vh;
    border-radius: 10px;
    color:rgb(0, 0, 0);
    background-color: aquamarine;
    margin-top: 5vh;
}
#imgAni{
    position: absolute;
    top: 0vh;
    left: 0vh;
    width: 29vw;
}
.container{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 2rem;
}
.container .changeBtn{
    width: 3vw;
    height: 7vh;
    margin: 0px 1rem;
}
.container .changeBtn:first-child{
    background-color: white;
    border-radius: 10px;
}

@media only screen and (max-width:600px){
    h1{
        font-size: 2rem;
        margin-top: 4rem;
    }
    @keyframes hTag {
        0%{
            left: 0vh;
        }
        25%{
            left:10vh;
        }
        50%{
            left:-10vh;
        }
        100%{
            left: 0vh;
        }
    }
    div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .container{
        margin-top: 1.5rem;
    }
    .container .changeBtn{
        width: 10vw;
        height: 7vh;
        margin: 0px 1rem;
    }
}